.sandbox {
  max-width: 1133px;
  margin: 32px auto;
}

.sandbox__sample {
  padding: 16px;
  border: 1px #e8e8e8 solid;
  border-radius: 3px;
  margin-bottom: 120px;
}

.sandbox__sample--show-margin > .panel {
  background-color: #ffb59e;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.sandbox__sample-box {
  background-color: #e3fbe9;
  border: 1px solid #cae4cf;
}

.sandbox samp {
  width: 100%;
  height: 100%;
  line-height: 32px;
  color: #666;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background-color: #f9f9f9;
}



